import { parseTime } from "@/utils";
export default {
  props: {
    city: String,
    date: Date,
  },
  computed: {
    dateText() {
      return parseTime(this.date, "{y}年{m}月{d}日");
    },
  },
  methods: {
    back() {
      this.$router.push("/operate");
    },
    onCityClick() {
      this.$emit("city");
    },
    onDateClick() {
      this.$emit("date");
    },
  },
};
